export default {
	data() {
		return {
			date: [], //购物车数据
			idue: "../../static/gou.png", //全选按钮
			kmaye: true, //全选按钮
			lakcu: "编辑", //编辑按钮
			kcnueg: false, //删除按钮
			gecture: false, //购物车为空时显示的
			jcgure: [], //购物车推荐列表
			icudee: false, //商品配置遮照层
			katce: 0.00, //总价格
			cuart: 1, //添加购物车数量
			msg: {}, //SKU数据
			cayrd: {}, //SKU配置的
			hcayr: '', //配置信息

		}
	},
	methods: {
		icude(e) { //全选按钮
			if (this.kmaye == true) {
				this.idue = "../../static/gou-red.png"
				this.kmaye = false
			} else {
				this.idue = "../../static/gou.png"
				this.kmaye = true
			}
		},
		gcyy: function() { //遮罩层
			this.icudee = false
		},
		haucarc: function(e) { //点击商品数量+1
			console.log(e)
			let _this = this;
			let car = e.id
			let vcer = e.num + 1
			e.num = e.num + 1
			let str = uni.getStorage({
				key: 'use'
			})

			str.then(function(result) { //获取购物车所有商品数据
				uni.request({
					url: "http://ceshi3.dishait.cn/api/cart/updatenumber/" + car + "",
					method: "POST",
					header: {
						'token': result[1].data
					},
					data: {
						num: vcer
					},
					success(data) {

					},
					fail(e) {
						console.log('报错')
					}
				})
			})
		},
		hacuge: function(e) { //点击商品数量-1
			let _this = this;
			let car = e.id
			let vcer = e.num - 1
			let str = uni.getStorage({
				key: 'use'
			})
			if (e.num == 1) {
				e.num = 1
			} else {
				e.num = e.num - 1
				str.then(function(result) { //获取购物车所有商品数据
					uni.request({
						url: "http://ceshi3.dishait.cn/api/cart/updatenumber/" + car + "",
						method: "POST",
						header: {
							'token': result[1].data
						},
						data: {
